@import 'src/assets/scss/variables';

.Prologue {
  min-height: 100vh;
  display: flex;
  align-items: center;
  margin-top: -4rem;

  .content {
    width: calc(100% - 20rem);
    margin-left: auto;
    margin-right: auto;
    position: relative;
    border-radius: 2rem 0 2rem 2rem;
    border: 1rem solid rgba(40, 44, 52, 0.5);

    img {
      max-width: 20rem;
      border-radius: 50%;
      box-shadow: 0px 5px 20px -10px #000;
      position: absolute;
      left: -0.7rem;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    .body {
      min-height: 50vh;
      padding: 2rem 8rem 2rem 12rem;
      display: flex;
      align-items: center;
    }

    .social {
      list-style: none;
      margin: 0;
      padding: 0;
      position: absolute;
      right: -0.7rem;
      top: 50%;
      transform: translate(50%, -50%);

      li {
        padding: 0.3rem 0;

        a {
          position: relative;
          display: block;
          padding: 0.5rem;
          border-radius: 0.5rem 0 0.5rem 0.5rem;
          background-color: rgba(40, 44, 52, 1);
          transition: 0.25s;

          &:hover {
            background-color: rgba(64, 72, 89, 1);
            transform: scale(1.1);
          }

          svg {
            height: 2.5rem;
            width: 3rem;
            color: white;
            fill: white;
            display: block;
          }
        }
      }
    }

    @media (max-width: 1080px) {
      width: calc(100% - 12rem);
      img {
        max-width: 13rem;
      }
      .body {
        padding-left: 8rem;
        padding-right: 4rem;
      }
    }

    @media (max-width: 800px) {
      width: calc(100% - 6rem);
      margin-top: 6rem;
      img {
        left: 50%;
        top: 0;
      }
      .body {
        padding-left: 2rem;
        padding-right: 2rem;
        padding-top: 7rem;
      }
    }

    @media (max-width: 490px) {
      width: calc(100% - 2rem);
      margin-left: 0;
    }
  }
}
